.up_btn {
    position: fixed;
    border-radius: 50%;
    box-shadow: 0 0 6px 4px rgba(254, 237, 145, 0.35);
    background-color: #49811e;
    right: 20px;
    bottom: 20px;
    cursor: pointer;
    width: 65px;
    height: 65px;
    z-index: 100;
    transition: 0.3s;
    display: none;
    @include rmin(1920) {
        right: calc( 50% - 900px ); }
    &::after {
        position: absolute;
        content: '';
        display: block;
        top: 28px;
        left: 21px;
        width: 20px;
        height: 20px;
        border-left: 2px solid #ffe44c;
        border-bottom: 2px solid #ffe44c;
        transform: rotate(135deg); } }


