.section-form {
    min-height: 493px;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    @include r($desktop) {
        min-height: auto;
        padding: 45px 0; } }
.section-form__container {
    display: flex;
    @include r($desktop) {
        flex-direction: column;
        min-height: auto; } }
.section-form__circle {
    margin-top: 140px;
    position: relative;
    max-width: 380px;
    width: 100%;
    margin-right: 120px;
    @include r($desktop) {
        margin-right: 0;
        max-width: 100%;
        margin-top: 0; }
    &::before {
        top: -184px;
        left: -160px;
        content: '';
        display: block;
        position: absolute;
        width: 598px;
        height: 603px;
        background: url(../img/circle-bg.png) no-repeat center;
        @include r($wide) {
            left: -240px; }
        @include r($desktop) {
            background: none;
            display: none; } } }
.section-form__circle-inner-content {
    position: relative;
    z-index: 2;
    @include r($desktop) {
        text-align: center; }
    .section-title {
        text-align: left;
        @include r($desktop) {
            text-align: center; }
        @include r($mobile) {
            font-size: 30px; } } }
.section-form__subtitle {
    color: #ffffff;
    font-family: "SegoeUI";
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    margin-top: 14px;
    margin-bottom: 14px;
    @include r($mobile) {
        font-size: 27px; } }
.section-form__circle-txt {
    margin-top: 11px;
    color: #ffffff;
    font-family: "SegoeUI";
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    &:first-of-type {
        margin-top: 0; } }
.section-form__wrap-form {
    margin-top: 48px;
    @include r($mobile) {
        margin-top: 25px; }
    .section-title {
        text-align: left;
        margin-bottom: 29px;
        @include r($desktop) {
            text-align: center; }
        @include r($mobile) {
            font-size: 30px; } }
    form {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        @include r($mobile) {
            flex-direction: column; } }
    .btn {
        max-width: 255px;
        @include r($desktop) {
            margin-left: auto;
            margin-right: auto; } }
    .politic-txt {
        margin-bottom: 26px;
        @include r($desktop) {
            text-align: center; } } }
.politic-txt {
    color: #ffffff;
    font-family: "SegoeUI";
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    a {
        color: #ffffff;
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit;

        text-decoration: underline; } }
.section-form__ui-group-submit, .section-form__ui-group-textarea {
    width: 100%; }
.section-form__ui-group-name, .section-form__ui-group-tel {
    width: calc( 50% - 10px );
    margin-bottom: 20px;
    @include r($mobile) {
        width: 100%; } }
.section-form_get-consult {
    .politic-txt {
        padding-right: 225px;
        @include r($desktop) {
            padding-right: 0; } } }
.section-form__ui-group-textarea {
    margin-bottom: 21px; }


.section-form_order-product {
    .section-form__wrap-form {
        margin-top: 89px;
        @include r($desktop) {
            margin-top: 30px; } }
    .politic-txt {
        max-width: 400px;
        @include r($wide) {
            max-width: 150px; }
        @include r($wide) {
            max-width: none;
            width: calc( 50% - 10px ); }
        @include r($mobile) {
            width: 100%; } } }
.section-form__ui-group-date {
    position: relative;
    margin-right: 25px;
    max-width: 255px;
    width: 100%;
    @include r($wide) {
        max-width: none;
        width: calc( 50% - 10px );
        margin-right: 0; }
    @include r($mobile) {
        width: 100%;
        margin-bottom: 20px; }
    &::after {
        width: 20px;
        content: url(../img/date.svg);
        display: block;
        position: absolute;
        top: calc( 50% + 2px );
        transform: translateY(-50%);
        right: 20px; } }
