.about-company {
    min-height: 704px;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding: 96px 0;
    @include r($tablet) {
        padding: 45px 0; } }
.about-company__section-title {
    margin-bottom: 55px;
    @include r($desktop) {
        margin-bottom: 30px; } }
.about-company__wrap {
    display: flex;
    align-items: center;
    @include r($wide) {
        align-items: flex-start; }
    @include r($desktop) {
        flex-direction: column;
        align-items: center; } }
.about-company__img {
    width: 480px;
    height: 410px;
    object-fit: cover;
    display: block;
    margin-right: 20px;
    box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.35);
    @include r($wide) {
        width: 300px;
        height: 250px; }
    @include r($desktop) {
        width: 500px;
        height: 415px;
        margin-right: 0;
        margin-bottom: 20px; }
    @include r($tablet) {
        width: auto;
        max-width: 100%;
        height: 300px; } }
.about-company__txt {
    p, h4 {
        color: #fff;
        margin-bottom: 20px;
        @include r($mobile) {
            margin-bottom: 15px; }
        &:last-child {
            margin-bottom: 0; } }


    h4 {
        font-weight: 700;
        font-size: 22px; }
    p {
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        margin-bottom: 22px;
        &:last-child {
            margin-bottom: 0; } } }
