.our-project {
    min-height: 615px;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding: 97px 0;
    @include r($mobile) {
        min-height: auto; } }
.our-project__section-title {
    margin-bottom: 42px; }
.our-project__wrap-slider {
    position: relative;
    @include r(1500) {
        padding-bottom: 60px; } }
.our-project__slider {
    margin-left: -10px;
    margin-right: -10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    .swiper-slide {
        box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.35);
        height: 320px;
        @include r($wide) {
            height: 280px; }
        @include r($tablet) {
            height: 220px; } }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        &::selection {
            background: none; } } }
.our-project__button-next, .our-project__button-prev {
    top: 50%;
    transform: translateY(-50%);
    @include r(1500) {
        transform: none;
        top: auto;
        bottom: 0; } }
.our-project__button-next {
    left: calc( 100% + 60px );
    @include r(1500) {
        left: calc( 50% + 40px ); } }
.our-project__button-prev {
    right: calc( 100% + 60px );
    @include r(1500) {
        right: calc( 50% + 40px ); } }
