* {
    box-sizing: border-box;
    outline: none; }

body, html {
    background: white;
    color: $fontColor;
    font-family: $font-family;
    font-size: 16px;
    min-width: 320px;
    max-width: 100%;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
.page-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
    max-width: 1920px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;

    // padding-bottom: 1000px
 }    // background-color: rgba(#000,0.5)
.container {
    max-width: 1180px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include r($wide) {
        max-width: 960px;
        padding-left: 15px;
        padding-right: 15px; }
    @include r($desktop) {
        max-width: 720px; }
    @include r($tablet) {
        max-width: 540px; }
    @include r($mobile) {
        max-width: 100%; } }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
    padding: 0;
    margin: 0; }
a {
    color: $fontColor;
    text-decoration: none;
    font-family: $font-family;
    font-size: 18px;
    font-weight: 400;
    &:hover, &:focus {
        text-decoration: none; } }
button {
    padding: 0;
    border: 0;
    box-shadow: none;
    background: none;
    cursor: pointer;
    &:focus {
        outline: none; } }
ul {
    list-style: none; }
ol {
    list-style: decimal; }
img.svg {
    display: none; }
.swiper-button-next, .swiper-button-prev {
    transform: none;
    margin: 0;
    padding: 0;
    top: auto;
    bottom: auto;
    left: auto;
    right: auto;
    background: none;
    color: transparent;
    &::after {
        display: none; } }
.btn {
    cursor: pointer;
    max-width: 280px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    border-radius: 22px;
    border: none;

    text-align: center;
    box-shadow: 0 0 6px 4px rgba(254, 237, 145, 0.35);
    background-color: #ffe44c;

    color: #101809;
    font-family: "SegoeUI";
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    transition: 0.3s;

    @include r($mobile) {
        height: 38px;
        font-size: 13px; }
    &:hover {
        background-color: #fff7ca; } }
.section-title {
    color: #ffffff;
    font-family: "SegoeUI";
    font-size: 51px;
    font-weight: 600;
    line-height: 1.1;
    text-align: center;
    @include r($desktop) {
        font-size: 45px; }
    @include r($tablet) {
        font-size: 40px; }
    @include r($mobile) {
        font-size: 32px; } }
.section-title_color_white {
    color: #fff; }
.section-title_color_black {
    color: #101809; }
.section-title_uppercase {
    text-transform: uppercase; }
.section-title_small {
    font-size: 40px;
    line-height: 1; }
.swiper-button-next, .swiper-button-prev {
    width: 54px;
    height: 54px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    svg {
        width: 13px;
        height: 25px; } }
.slider-btn_theme_white {
    box-shadow: 0 0 6px 4px rgba(254, 237, 145, 0.35);
    background-color: #ffffff;
    svg {
        fill: #49811e; } }
.slider-btn_theme_dark {
    box-shadow: 0 0 6px 4px rgba(254, 237, 145, 0.35);
    background-color: #49811e;
    svg {
        fill: #ffe44c; } }
