.how-we-work {
    padding: 95px 0;
    @include r($tablet) {
        padding: 45px 0; } }
.how-we-work__wrap {
    display: flex;
    @include r($desktop) {
        flex-direction: column; } }
.how-we-work__l, .how-we-work__r {
    width: 50%;
    @include r($desktop) {
        width: 100%; } }
.how-we-work__l {
    margin-top: 47px;
    padding-right: 50px;
    @include r($wide) {
        padding-right: 30px;
        display: flex;
        flex-direction: column;
        align-items: center; }
    @include r($mobile) {
        padding-right: 0; } }
.how-we-work__subtitle {
    color: #101809;
    font-family: "SegoeUI";
    font-size: 30px;
    font-weight: 400;
    line-height: 26px;
    @include r($desktop) {
        text-align: center; } }
.how-we-work__l-items {
    margin-top: 32px; }
.how-we-work__l-item {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    @include r($mobile) {
        flex-direction: column;
        margin-bottom: 40px; }
    &:last-child {
        margin-bottom: 0; } }
.how-we-work__l-item-img {
    margin-right: 20px;
    width: 220px;
    height: 190px;
    display: block;
    object-fit: cover;
    box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.35);
    @include r($mobile) {
        margin-right: 0;
        margin-bottom: 10px; } }
.how-we-work__l-item-title {
    font-family: "SegoeUI";
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    color: #101809;
    span {
        color: #49811e;
        display: block;
        @include r($desktop) {
            display: inline; } } }
.how-we-work__l-have-question {
    margin-top: 55px;
    max-width: 423px;
    width: 100%;
    @include r($desktop) {
        text-align: center; }
    @include r($mobile) {
        margin-top: 40px; } }
.how-we-work__l-have-question-title {
    color: #49811e;
    font-size: 30px;
    font-weight: 400;
    line-height: 26px; }
.how-we-work__l-have-question-desc {
    color: #101809;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    margin-top: 10px; }
.how-we-work__btn {
    margin-top: 14px;
    max-width: 255px;
    @include r($desktop) {
        margin-left: auto;
        margin-right: auto; } }
.how-we-work__r {
    margin-top: 68px;
    @include r($wide) {
        padding-left: 90px; }
    @include r($mobile) {
        margin-top: 30px;
        padding-left: 0; } }
.how-we-work__r-item {
    margin-bottom: 61px;
    position: relative;
    @include rmin($mobile) {
        &:first-child, &:last-child {
            margin-left: 70px; }
        &:nth-child(2), &:nth-last-child(2) {
            margin-left: 20px; } }
    @include r($mobile) {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 320px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 40px; }
    &:last-child {
        margin-bottom: 0; } }
.how-we-work__r-item-wrap-icon {
    position: absolute;
    width: 125px;
    height: 118px;
    background: url(../img/advantages-wrap-icon.svg) no-repeat center;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -26px;
    right: calc( 100% + 20px  );
    @include r($mobile) {
        position: static;
        margin-bottom: 10px; } }
.how-we-work__r-item-icon {
    display: block;
    margin-top: 7px;
    width: 34px;
    height: 32px; }
.how-we-work__r-item-title, .how-we-work__r-item-desc {
    color: #101809;
    line-height: 26px;
    @include r($mobile) {
        text-align: center; } }
.how-we-work__r-item-title {
    font-weight: 700;
    font-size: 22px;
    margin-bottom: 9px; }
.how-we-work__r-item-desc {
    font-weight: 400;
    font-size: 18px; }


