.mfp-wrap {
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translateZ(0); }
html.mfp-helper {
    height: 100%;
    body {
        overflow: hidden;
        height: 100%;
        -webkit-transform: translateZ(0); } }
.mfp-close-btn-in {
    .mfp-close {
        top: 20px;
        right: 20px;
        width: 18px;
        height: 18px;
        background: url(../img/close.png) no-repeat center / 100% 100%;
        color: transparent; } }
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
    width: inherit;
    min-width: 33px; }
.white-popup-block {
    max-width: 500px;
    width: 100%;
    text-align: left;
    margin: auto;
    position: relative;
    background: url(../img/modal-bg.jpg) no-repeat center / cover;
    @include r($wide) {
        background-position: right center;
        background-size: 200%; }
    @include r($desktop) {
        background-size: cover; } }
.popup-buble {
    .popup-dialog {
        opacity: 0;
        transition: all 0.2s ease-in-out;
        transform: scale(0.8); }
    &.mfp-ready .popup-dialog {
        opacity: 1;
        transform: scale(1); }
    &.mfp-removing .popup-dialog {
        transform: scale(0.8);
        opacity: 0; }
    &.mfp-bg {
        background: rgba(0, 0, 0, 0.6);
        opacity: 0;
        transition: 0.3s; }
    &.mfp-ready.mfp-bg {
        opacity: 1; }
    &.mfp-removing.mfp-bg {
        opacity: 0; } }
.modal-form {
    max-width: 1180px;
    @include r($desktop) {
        padding-bottom: 30px; }
    .popup-dialog {
        position: relative;
        display: flex;
        justify-content: space-between;
        min-height: 493px;
        padding-left: 180px;
        padding-right: 100px;
        @include r($wide) {
            padding-left: 100px; }
        @include r($desktop) {
            flex-direction: column;
            align-items: center;
            min-height: auto; }
        @include r($tablet) {
            padding-left: 20px;
            padding-right: 20px; }
        &::before {
            content: '';
            display: block;
            position: absolute;
            top: -43px;
            left: 20px;
            background: url(../img/circle-bg.png) no-repeat center;
            width: 598px;
            height: 603px;
            @include r($wide) {
                background: none;
                display: none; } } } }
.modal-form__l {
    margin-top: 180px;
    max-width: 400px;
    width: 100%;
    position: relative;
    z-index: 2;
    @include r($wide) {
        margin-top: 140px; }
    @include r($desktop) {
        margin-top: 50px; }
    .section-title {
        text-align: left;
        @include r($desktop) {
            text-align: center; }
        @include r($mobile) {
            font-size: 32px;
            line-height: normal; } } }
.modal-form__subtitle {
    margin-top: 40px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    line-height: 40px;
    @include r($desktop) {
        margin-top: 20px;
        text-align: center; }
    @include r($tablet) {
        font-size: 19px;
        line-height: normal; } }
.modal-form__r {
    margin-top: 115px;
    max-width: 380px;
    width: 100%;
    @include r($desktop) {
        margin-top: 20px; }
    .politic-txt {
        margin-bottom: 24px; } }
.modal-form__ui-group-submit {
    .btn {
        max-width: 100%; } }
.modal-form__ui-group-name, .modal-form__ui-group-tel {
    margin-bottom: 20px; }
