input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
    -webkit-appearance: none;
    width: 100%;
    border-radius: 25px;
    background-color: #fff;
    border: none;
    &::-webkit-input-placeholder {
        color: #4e4e4e;
        font-family: "SegoeUI";
        font-size: 15px;
        font-weight: 400; }
    &::-moz-placeholder {
        color: #4e4e4e;
        font-family: "SegoeUI";
        font-size: 15px;
        font-weight: 400; }
    &:-ms-input-placeholder {
        color: #4e4e4e;
        font-family: "SegoeUI";
        font-size: 15px;
        font-weight: 400; } }
input[type="text"],
input[type="tel"],
input[type="email"] {
    height: 50px;
    padding: 0 20px; }
/* Для Firefox */
input[type=number] {
  -moz-appearance: textfield;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }
textarea {
    resize: none;
    height: 110px;
    padding: 20px; }
