.why-we {
    padding: 93px 0 43px 0;
    @include r($tablet) {
        padding: 45px 0; } }
.why-we__section-title {
    margin-bottom: 39px; }
.why-we__container {
    position: relative;
    &::after {
        content: url(../img/why-we-decor.jpg);
        display: block;
        position: absolute;
        top: 135px;
        left: calc( 100% - 80px );
        @include r($wide) {
            content: ''; } } }
.why-we__wrap {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px; }
.why-we__item {
    width: calc( 100% / 3 - 20px );
    margin: 0 10px 50px 10px;
    @include r($wide) {
        width: calc( 50% - 20px ); }
    @include r($tablet) {
        width: 100%;
        margin: 0 0 40px 0; } }
.why-we__item-top-line {
    display: flex;
    @include r($tablet) {
        align-items: center; } }
.why-we__item-icon {
    width: 50px;
    height: 60px;
    margin-right: 18px; }
.why-we__item-title {
    color: #101809;
    font-family: "SegoeUI";
    font-size: 22px;
    font-weight: 700;
    line-height: 26px; }
.why-we__item-txt {
    margin-top: 10px;
    color: #101809;
    font-family: "SegoeUI";
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: -0.1px; }
