.footer {
    padding: 80px 0 14px 0;
    @include r($desktop) {
        padding-top: 30px; }
    @include r($mobile) {
        padding-top: 15px; } }
.footer__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include r($mobile) {
        flex-direction: column;
        align-items: center; } }
.footer__copy {
    color: #101809;
    font-family: "SegoeUI";
    font-size: 20px;
    font-weight: 400;
    @include r($mobile) {
        margin-bottom: 10px; } }
