.header_scroll {
    @include rmin($mobile) {
        background-color: rgba(#001c01,0.5); } }
.header {
    position: fixed;
    padding: 5px 0;
    max-width: inherit;
    z-index: 100;
    width: 100%;
    @include r($mobile) {
        position: absolute;
        padding: 0; } }
.header__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
.header__logo {
    width: 88px;
    height: auto;
    max-height: 101px;
    @include r($tablet) {
        width: 70px; }
    @include r($mobile) {
        margin-top: 70px;
        margin-left: auto;
        margin-right: auto;
        display: block; } }
.header__action {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 515px;
    width: 100%;
    @include r($tablet) {
        max-width: 415px; }
    @include r($mobile) {
        max-width: 100%;
        position: fixed;
        z-index: 100;
        top: 0;
        left: 0;
        padding: 6px;
        background-color: rgba(#001c01,0.5); } }
.header__wrap-tel {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    &:last-child {
        margin-bottom: 0; } }
.header__icon {
    width: 21px;
    height: 21px;
    margin-right: 12px;
    @include r($mobile) {
        display: none; }
    svg {
        fill: #ffe44c;
        width: 100%;
        height: 100%; } }
.header__tel {
    color: #ffffff;
    font-family: "SegoeUI";
    font-size: 20px;
    font-weight: 600;
    @include r($mobile) {
        font-size: 16px;
        letter-spacing: -0.2px; }
    &:hover {
        text-decoration: underline; } }
.header__btn {
    @include r($tablet) {
        max-width: 200px; }
    @include r($mobile) {
        max-width: 160px; } }
