.first-screen {
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    min-height: 900px;
    padding-top: 174px;
    @include r($mobile) {
        min-height: auto;
        padding-bottom: 50px; }
    p, h1 {
        color: #fff;
        text-align: center; } }
.first-screen__section-title {
    @include r($mobile) {
        font-size: 27px; } }
.first-screen__subtitle {
    margin-top: 35px;
    margin-left: auto;
    margin-right: auto;
    max-width: 500px;
    width: 100%;
    font-size: 41px;
    font-weight: 600;
    line-height: 58px;
    @include r($mobile) {
        font-size: 23px;
        line-height: normal; }
    sup {
        font-size: 25px;
        @include r($mobile) {
            font-size: 14px; } } }
.first-screen__subtitle_color_yellow {
    color: #ffe44c; }
.first-screen__subtitle_ruble {
    font-size: 0.7em;
    font-weight: 400; }
.first-screen__we-do {
    margin-top: 44px;
    font-size: 22px;
    font-weight: 700;
    line-height: 36px;
    @include r($mobile) {
        margin-top: 25px;
        font-size: 16px;
        font-weight: 600;
        line-height: normal; } }
.first-screen__btn {
    margin-top: 67px;
    margin-left: auto;
    margin-right: auto;
    @include r($mobile) {
        margin-top: 35px; } }
