.advantages {
    padding: 95px 0;
    position: relative;
    @include r($tablet) {
        padding: 45px 0; }
    &::before {
        content: url(../img/advantages-bg.png);
        position: absolute;
        top: 110px;
        left: 0;
        @include r($wide) {
            content: ''; } } }
.advantages__container {
    position: relative; }
.advantages__section-title {
    text-align: center;
    margin-bottom: 42px;
    @include r($wide) {
        margin-bottom: 25px; } }
.advantages__wrap {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -10px;
    position: relative;
    z-index: 2;
    @include r($wide) {
        margin-top: -80px; }
    @include r($tablet) {
        margin-top: -40px;
        margin-left: 0;
        margin-right: 0;
        justify-content: space-between; } }
.advantages__item {
    max-width: 280px;
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
    @include rmin($wide) {
        &:nth-child(2), &:nth-child(7) {
            max-width: 380px; }
        &:nth-child(4) {
            margin-right: 25.5%; }
        &:nth-child(5) {
            margin-left: 25.5%; }
        &:nth-last-child(n+4) {
            margin-bottom: 105px; } }
    @include r($wide) {
        margin-top: 15px;
        margin-bottom: 15px; }
    @include r($tablet) {
        margin-left: 0;
        margin-right: 0;
        width: calc( 50% - 10px ); } }
.advantages__item-wrap-icon {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 126px;
    height: 118px;
    background: url(../img/advantages-wrap-icon.svg) no-repeat center / 100% 100%;
    img {
        margin-top: 6px;
        width: 40px;
        height: 32px; } }
.advantages__item-title {
    margin-top: 21px;
    color: #101809;
    font-family: "SegoeUI";
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    line-height: 26px; }
.advantages__img {
    top: 244px;
    left: 250px;
    position: absolute;
    width: 646px;
    height: 456px;
    @include r($wide) {
        display: block;
        position: static;
        max-width: 650px;
        width: 100%;
        height: auto;
        margin-left: auto;
        margin-right: auto; } }
