.review {
    padding: 98px 0;
    @include r($tablet) {
        padding: 45px 0; } }
.review__section-title {
    margin-bottom: 40px;
    @include r($mobile) {
        margin-bottom: 20px; } }
.review__wrap-slider {
    @include r(1500) {
        padding-bottom: 80px; } }
.review__slider {
    box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.15);
    background-color: #ffffff;
    .swiper-slide {
        padding: 20px;
        display: flex;
        @include r($desktop) {
            flex-direction: column; }
        @include r($mobile) {
            padding: 10px; } } }
.review__slide-img {
    width: 660px;
    height: 370px;
    object-fit: cover;
    margin-right: 20px;
    @include r($wide) {
        width: 515px; }
    @include r($desktop) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px; }
    @include r($mobile) {
        height: 250px; } }
.review__slide-wrap-txt {
    margin-top: 3px; }
.review__slide-title {
    color: #101809;
    font-family: "SegoeUI";
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    margin-bottom: 10px; }
.review__slide-txt {
    color: #000000;
    font-family: "SegoeUI";
    font-size: 18px;
    font-weight: 400;
    line-height: 26px; }
.review__wrap-slider {
    position: relative; }
.review__button-next, .review__button-prev {
    top: 50%;
    transform: translateY(-50%);
    @include r(1500) {
        transform: none;
        top: auto;
        bottom: 0; } }
.review__button-next {
    left: calc( 100% + 60px );
    @include r(1500) {
        left: calc( 50% + 40px ); } }
.review__button-prev {
    right: calc( 100% + 60px );
    @include r(1500) {
        right: calc( 50% + 40px ); } }
